<template>
    <div class="container">
        <div class="form-box">
            <el-form ref="form" :model="form" label-width="80px">
                <el-form-item label="文章名称">
                    <el-input v-model="form.title" placeholder="请输入文章名称" style="width: 375px;"></el-input>
                </el-form-item>
                <el-form-item label="文章类型" required>
                    <el-select v-model="form.categoryId" style="width: 375px;" placeholder="请选择">
                        <el-option class="search-item-inp" v-for="item in categoryTheList" :key="item.value" :label="item.label" :value="item.value" style="width: 375px;">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="内容">
                    <div class="editor-box">
                        <Toolbar style="border-bottom: 1px solid #ccc" :editor="editor" :defaultConfig="toolbarConfig" :mode="mode" />
                        <Editor style="height: 500px; overflow-y: hidden;" v-model="html" :defaultConfig="editorConfig" :mode="mode" @onCreated="onCreated" />
                    </div>
                    <div>*文章页会有20像素边距，建议视频和图片的宽度最大不设置超过355,<br>web端和小程序端效果有些许差异，已小程序为主</div>
                </el-form-item>
                <el-form-item label="">
                    <el-button @click="save" type="primary">保存</el-button>
                    <el-button @click="dialogVisible = true">预览</el-button>
                    <el-button @click="back">返回</el-button>
                </el-form-item>
            </el-form>
        </div>
        <el-dialog title="预览" :width="'410px'" :visible.sync="dialogVisible" @close="close">
            <div class="dialog-modal-container">
                <div style="margin-bottom: 20px">{{form.title}}</div>
                <div v-html="html" class="html-box" style=""></div>
            </div>
        </el-dialog>

    </div>
</template>

<script>
import { Editor, Toolbar } from '@wangeditor/editor-for-vue';
import { articleCreate, categoryList, articleDetail, articleUpdate } from '@/api/article';
import dev from '@/utils/dev';

const imgTypeList = ['png', 'jpg', 'jpeg'];
const videoType = ['mp4', 'avi'];

export default {
    components: { Editor, Toolbar },
    data () {
        return {
            form: {
                title: '',
                categoryId: null,
            },
            editor: null,
            html: '',
            toolbarConfig: {},
            editorConfig: {
                placeholder: '请输入内容...',
                MENU_CONF: {
                    uploadImage: {
                        server: `${dev.URL}/file/upload/cos`,
                        // server: 'http://localhost:5001/imgUpload',
                        headers: {
                            'ysxf-auth-data': this.$store.state.userData,
                            'ysxf-upload-data': JSON.stringify({ category: 'article' })
                        },
                        maxFileSize: 1000 * 1024 * 1024, // 10M
                        // 上传错误，或者触发 timeout 超时
                        onBeforeUpload: file => {
                            let data = null;
                            Object.keys(file).forEach(item => {
                                if (item) data = file[item];
                            });
                            if (data.size > (10 * 1024 * 1024)) {
                                this.errorTip(`上传文件不能超过10M`);
                                return false;
                            }
                            if (imgTypeList.indexOf(data.extension) == -1) {
                                this.errorTip(`上传格式错误,只允许上传${imgTypeList.join(',')}格式`);
                                return false;
                            }
                            return file;

                        },
                        customInsert: (res, insertFn) => {
                            console.log(res.data.id);
                            if (res.code != 0) {
                                this.errorTip('上传失败');
                                return;
                            }
                            if (res.code == 0) {
                                this.fileIdList.push(res.data.id);
                                // // 从 res 中找到 url alt href ，然后插入图片
                                insertFn(`http://${res.data.url}`, '', '');
                            }
                        }
                    },
                    uploadVideo: {
                        server: `${dev.URL}/file/upload/cos`,
                        // server: 'http://localhost:5001/imgUpload',
                        headers: {
                            'ysxf-auth-data': this.$store.state.userData,
                            'ysxf-upload-data': JSON.stringify({ category: 'article' })
                        },
                        maxFileSize: 1000 * 1024 * 1024, // 10M
                        // 上传错误，或者触发 timeout 超时
                        onBeforeUpload: file => {
                            let data = null;
                            Object.keys(file).forEach(item => {
                                if (item) data = file[item];
                            });
                            if (videoType.indexOf(data.extension) == -1) {
                                this.errorTip(`上传格式错误,只允许上传${videoType.join(',')}格式`);
                                return false;
                            }
                            if (data.size > (10 * 1024 * 1024)) {
                                this.errorTip(`上传文件不能超过10M`);
                                return false;
                            }
                            return file;

                        },
                        customInsert: (res, insertFn) => {
                            console.log(res.data.id);
                            if (res.code != 0) {
                                this.errorTip('上传失败');
                                return;
                            }
                            if (res.code == 0) {
                                this.fileIdList.push(res.data.id);
                                // // 从 res 中找到 url alt href ，然后插入图片
                                insertFn(`http://${res.data.url}`, '', '');
                            }
                        }
                    }
                }
            },
            mode: 'default', // or 'simple'
            dialogVisible: false,
            categoryTheList: [],
            fileIdList: [],
        };
    },
    computed: {
        userData () {
            return this.$store.state.userData;
        }
    },
    methods: {
        errorTip (str) {
            this.$message.error(str);
        },
        categoryList () {
            categoryList({ isPaging: false }).then(res => {
                if (res.data.code == 0) {
                    this.categoryTheList = res.data.data.rows.map(item => {
                        return { value: item.id, label: item.name };
                    });
                }
            });
        },
        onCreated (editor) {
            this.editor = Object.seal(editor); // 一定要用 Object.seal() ，否则会报错
        },
        save () {
            if (!this.form.title) {
                this.$message.warning('请输入标题');
                return;
            }
            if (!this.form.categoryId) {
                this.$message.warning('请选择文章分类');
                return;
            }
            if (this.type == 'add') {
                this.createArticle();
            } else {
                this.articleUpdate();
            }
        },
        articleUpdate () {
            const { title, categoryId } = this.form;
            articleUpdate({
                content: this.html,
                fileIdList: this.fileIdList.join(','),
                title,
                categoryId: categoryId,
                id: Number(this.id),
            }).then(res => {
                console.log(res.data);
                if (res.data.code == 0) {
                    this.$message.success('更新成功');
                    setTimeout(() => {
                        this.back();
                    }, 2000);
                } else {
                    this.$message.error(`${res.data.message},${res.data.detail}`);
                }
            });
        },
        createArticle () {
            const { title, categoryId } = this.form;
            articleCreate({
                content: this.html,
                fileIdList: this.fileIdList.join(','),
                title,
                categoryId: categoryId
            }).then(res => {
                console.log(res.data);
                if (res.data.code == 0) {
                    this.$message.success('新增成功');
                    setTimeout(() => {
                        this.back();
                    }, 2000);
                } else {
                    this.$message.error(`${res.data.message},${res.data.detail}`);
                }
            });
        },
        back () {
            this.$router.go(-1);
        },
        getDetails () {
            articleDetail({ id: `${this.id}`, categoryId: this.categoryId }).then(res => {
                const { content, title, categoryId } = res.data.data.articleRow;
                this.form.title = title;
                this.html = content;
                this.form.categoryId = categoryId;
            });
        },
        close () {
            this.dialogVisible = false;
            const videos = document.querySelectorAll('video');
            if (videos) {
                videos.forEach(item => {
                    item.pause();
                });
            }
        }
    },
    created () {
        this.categoryList();
        const { type, id, categoryId } = this.$route.query;
        this.type = type;
        if (type == 'edit' && id) {
            this.id = id;
            this.categoryId = categoryId;
            this.getDetails();
        }
    },
    mounted () {

        // 模拟 ajax 请求，异步渲染编辑器
        setTimeout(() => {
            // this.html = '<p><br></p><p><br></p><h1 style="text-align: center;"><span style="font-size: 15px;">疫情期间</span></h1><h1 style="text-align: center;"><span style="font-size: 15px;">宅家办税有妙招 </span></h1><p style="text-align: center;"><img src="https://mmbiz.qpic.cn/mmbiz_jpg/dY5b3JQjq3wcHqcYJ2n5d7Oy0wmdV145LNZMic7zX7BzDvkOmgQNXvoLpn7mHp5dDiagmQJbVoLPgLGmY49MljmA/640?wx_fmt=jpeg&amp;wxfrom=5&amp;wx_lazy=1&amp;wx_co=1" alt="图片" data-href="" style="width: 115px !important;height: auto !important;"></p><p style="text-align: center;"><span style="color: rgb(72, 140, 250);"><strong>天河区税务局云税厅</strong></span></p><p style="text-align: center;"><span style="color: rgb(72, 140, 250);"><strong>为您提供多种“非接触式”办税缴费服务</strong></span></p><p style="text-align: center;"><span style="font-size: 15px;">办税上“云”端、服务不“打烊”</span></p><h1 style="text-align: center;"></h1><p style="text-align: center;"><strong>一</strong></p><p style="text-align: center;"><strong>电子税务局</strong></p><p style="text-align: center;"><strong><br></strong></p><p style="text-align: center;"><br></p><p><span style="font-size: 15px;">1、广东省电子税务局</span></p><p><span style="font-size: 15px;">https://etax.guangdong.chinatax.gov.cn/xxmh </span></p><p><br></p><p><span style="font-size: 15px;">2、自然人电子税务局</span></p><p><span style="font-size: 15px;">https://etax.chinatax.gov.cn</span></p><p style="text-align: justify;"><strong>二</strong></p><p style="text-align: justify;"><strong>APP或小程序</strong></p><p style="text-align: justify;"><strong><br></strong></p><p style="text-align: justify;"><br></p><p style="text-align: justify;"><span style="font-size: 15px;">长按或扫描识别以下二维码，下载APP或使用微信小程序，办理所需业务。</span></p><p style="text-align: justify;"><br></p><p style="text-align: center;"><img src="https://mmbiz.qpic.cn/mmbiz_png/dY5b3JQjq3wcHqcYJ2n5d7Oy0wmdV145BDU5hsUGGXAibDVEibmswPyhc41QefWBbHicUMkfkAxKn1hM7gK9AoGvA/640?wx_fmt=png&amp;wxfrom=5&amp;wx_lazy=1&amp;wx_co=1" alt="图片" data-href="" style="width: 143px !important;height: auto !important;"></p><p style="text-align: center;"><br></p><p style="text-align: center;"><img src="https://mmbiz.qpic.cn/mmbiz_png/dY5b3JQjq3wcHqcYJ2n5d7Oy0wmdV145hFFbsT7aLK67QJq4lRzOgG8ken1n013BcaypEgScwnbxf5VHFwySEw/640?wx_fmt=png&amp;wxfrom=5&amp;wx_lazy=1&amp;wx_co=1" alt="图片" data-href="" style="width: 130px !important;height: auto !important;"></p><p style="text-align: center;"><br></p><p style="text-align: center;"><img src="https://mmbiz.qpic.cn/mmbiz_png/dY5b3JQjq3wcHqcYJ2n5d7Oy0wmdV145ksLG9DhtzibNrPFT0Rnz0PakyputMLOQOfwCQaJ9bJbtQKdD71TXuug/640?wx_fmt=png&amp;wxfrom=5&amp;wx_lazy=1&amp;wx_co=1" alt="图片" data-href="" style="width: 127px !important;height: auto !important;"></p><p style="text-align: justify;"><strong>三</strong></p><p style="text-align: justify;"><strong>V-Tax远程可视化办税平台</strong></p><p style="text-align: justify;"><strong><br></strong></p><p><br></p><p><span style="font-size: 15px;">如您需要与办税服务厅工作人员面对面进行沟通，可通过V-Tax远程可视化办税平台办理。通过该平台，您可选择在线帮办或离线帮办税费业务，线上传递资料、线上接收办理结果。</span></p><p><br></p><p style="text-align: center;"><strong>【V-Tax平台登录方式】</strong></p><p style="text-align: center;"><br></p><p style="text-align: center;"><strong>PC端</strong></p><p><span style="font-size: 15px;">https://nsfw.guangdong.chinatax.gov.cn/zndb/poc/#/systemSelect</span></p><p><br></p><p style="text-align: center;"><strong>手机端</strong></p><p><span style="font-size: 15px;">微信－“粤税通”小程序－智能预约－按办理业务页面提示选择“远程可视化办税”</span></p><p><br></p><p style="text-align: center;"><br></p><p><br></p><p><span style="color: rgb(171, 25, 66);"><strong>注意啦！</strong></span><span style="background-color: transparent; font-size: 15px;">如您必须到办税服务厅办理业务，请通过</span><span style="color: rgb(0, 128, 255); background-color: transparent; font-size: 15px;"><strong>“粤税通”小程序</strong></span><span style="background-color: transparent; font-size: 15px;">提前预约。以下同城通办业务，您可就近选择广州市内任一开放的办税服务厅办理。</span></p><p><br></p><p style="text-align: center;"><span style="font-size: 15px;">（请滑动查清单全部内容↓↓）</span></p><p style="text-align: center;"><br></p><p><br></p><p style="text-align: center;"><br></p><p><br></p><p style="text-align: center;"><br></p><p><br></p><p>温馨提示<br></p><p><br></p><p><br></p><p><br></p><p><br></p><p><br></p><p><br></p><p><br></p><p><br></p><p><br></p><p><br></p><p><span style="font-size: 15px;">进入办税服务厅的办事群众，须按当地区域疫情防控要求提供核酸阴性证明，按要求在入口处扫描“场所码”、查验健康码及行程码。健康码显示红码或黄码的人员、未佩戴口罩或体温异常等不适症状者，不得进入办税服务厅。感谢您的配合！</span></p><p style="text-align: center;"><br></p><p style="text-align: center;"><span style="color: rgb(171, 25, 66); font-size: 15px;">快转给身边的人吧~</span><br></p><p style="text-align: center;"><br></p><p><br></p><p><br></p><p><img src="https://mmbiz.qpic.cn/mmbiz_gif/dY5b3JQjq3y1aIWWwCZz6RShvaiaCswTJfFMqYxZbLerPRiavnSPFKmonqcYzqb3I0nCgXBIpjQChjKNaLw2CgBg/640?wx_fmt=gif&wxfrom=5&wx_lazy=1" alt="图片" data-href="" style="width: 125px !important;height: auto !important;"/></p><p><br></p><p><span style="color: rgb(127, 127, 127); font-size: 14px;">素材来源：国家税务总局广州市税务局、国家税务总局广州市天河区税务局</span></p><p><span style="color: rgb(127, 127, 127); font-size: 14px;">出品：天河区融媒体中心</span></p><p><span style="color: rgb(127, 127, 127); font-size: 14px;">编辑：许智君</span></p><p><span style="color: rgb(127, 127, 127); font-size: 14px;">转载请注明：广州天河发布</span></p><p><span style="color: rgb(127, 127, 127); font-size: 14px;">投稿邮箱：gzthfb@163.com</span></p><p style="text-align: center;"><br></p><p style="text-align: center;"><br></p><p><br></p><div data-w-e-type="video" data-w-e-is-void><video poster="" controls="true" width="350" height="auto"><source src="https://wx.zone139.com/2016tyjf_dev/qqtqyzx/upload/qqtgy/ueditor/video/20220412/1649745739519046697.mp4" type="video/mp4"/></video></div><p style="text-align: center;"><br></p>';
        }, 1500);
        // console.log(this.editorConfig);
        // this.editorConfig.MENU_CONF['uploadImage'] = {
        //     server: 'http://localhost:5001/imgUpload',
        // };
        // this.editorConfig.MENU_CONF['uploadImage'] = {
        //     server: 'http://localhost:5001/imgUpload',//上传接口地址
        //     headers: {
        //     },
        //     fieldName: 'file'//上传文件名
        // };
        // this.html = '';
    },
    beforeDestroy () {
        const editor = this.editor;
        if (editor == null) return;
        editor.destroy(); // 组件销毁时，及时销毁编辑器
    }
}
</script>

<style scoped>
