.editor-box {
    width: 375px;
    border: 1px solid #ccc;
}
.dialog-modal-container {
    display: flex;
    flex-direction: column;
}
.html-box {
    width: 375px;
    border: 1px solid #ccc;
    /* padding: 10px; */
